<template>
  <Tabs @scroll="onScroll" :offset-top="titleBarHeight" sticky :key="somekey">
    <Tab v-for="item in tabs" :key="item.type" :title="item.title">
      <van-list
        v-model:loading="item.loading"
        :finished="item.finished"
        finished-text="没有更多了"
        @load="onLoadWith(item)"
      >
        <MovieCell
          v-for="movie in item.movies"
          :key="movie.filmId"
          :movie="movie"
          :clickAction="handleSelectMovie"
        />
      </van-list>
    </Tab>
  </Tabs>
</template>

<script setup>
import { reactive, ref } from 'vue'
import MovieCell from '@/components/movie_components/MovieCell.vue'
import { initMovieList } from '@/composables/initMovies.js'
import { useStore } from 'vuex'
import { Tab, Tabs } from 'vant'
import router from '@/router/index.js'

const store = useStore()
const { cityId } = store.state.currentCity

const tabs = reactive([
  { title: '正在热映', type: 1 },
  { title: '即将上映', type: 2 },
])
tabs.forEach((item) => {
  item['cityId'] = cityId
  item['movies'] = []
  item['pageNum'] = 1
  item['pageSize'] = 10
  item['loading'] = false
  item['finished'] = false
})

const onLoadWith = (params) => {
  initMovieList(params)
}
// eslint-disable-next-line no-undef
const props = defineProps({
  titleBarHeight: Number,
  getScrollTop: Function,
})

//注册监听滚动距离
const onScroll = (swipe) => {
  props.getScrollTop(swipe.scrollTop)
}

//选择电影的回调
const handleSelectMovie = (type, filmId) => {
  if (type === '电影详情') {
    router.push(`/detail/${filmId}`)
  } else if (type === '购票') {
    router.push({
      name: 'tickets',
      params: { filmId: filmId },
    })
  }
}

const somekey = ref(0)
somekey.value = 1
</script>
