<template>
  <div>
    <!-- 选择城市按钮 -->
    <button
      v-if="imgRef"
      class="citySelector"
      :style="[citySelectorStyle, dynamicCitySelector]"
      @click="goCityPage"
    >
      {{ cityName }}
    </button>
    <!-- 标题栏 -->
    <div class="titlebar" :style="[titleBarStyle, dynamicTitleBar]">标题</div>
    <!-- 轮播 -->
    <Swipe
      class="my-swipe"
      :loop="false"
      lazy-render
      indicator-color="white"
      placeholder
    >
      <SwipeItem v-for="item in bannerList" :key="item.bannerId">
        <img ref="imgRef" class="banner-image" :src="item.imgUrl" />
      </SwipeItem>
    </Swipe>
    <!-- 分页列表 -->
    <TabLists
      class="tab-list"
      v-if="imgRef"
      :titleBarHeight="titleBarHeight"
      :getScrollTop="getScrollTop"
    />
  </div>
</template>

<script setup>
import TabLists from '@/components/movie_components/TabLists.vue'
import { initBannerList } from '@/composables/initMovies.js'
import { Swipe, SwipeItem } from 'vant'
import { computed, ref } from '@vue/reactivity'
import store from '@/store/'
import router from '@/router/'
import { watch } from '@vue/runtime-core'
import { useWindowSize } from '@vant/use'

const { width } = useWindowSize()

const {
  currentCity: { name: cityName, cityId },
} = store.state

const titleBarHeight = 50
const citySelectorHeight = 25

const bannerList = ref([])
const type = 2
const k = 6446113
initBannerList(bannerList, type, cityId, k)

// 分页列表滑动的百分比
const percentage = ref()

// 监听视口宽度,更新bannerHeight
const bannerHeight = ref(document.documentElement.clientWidth * 0.56)
watch(width, (width) => {
  bannerHeight.value = width * 0.56
})
// 监听分页列表滑动事件
const getScrollTop = (tabBarScrollTop) => {
  percentage.value = tabBarScrollTop / (bannerHeight.value - titleBarHeight)
  percentage.value = percentage.value >= 1 ? 1 : percentage.value
}

// 绑定样式
const titleBarStyle = {
  height: titleBarHeight + 'px',
  lineHeight: titleBarHeight + 'px',
  transform: `translateY(${-titleBarHeight}px)`,
}
const citySelectorStyle = {
  height: `${citySelectorHeight}px`,
  top: `${titleBarHeight / 2 - citySelectorHeight / 2}px`,
}

// 动态样式
const dynamicTitleBar = computed(() => {
  return { top: percentage.value * titleBarHeight + 'px' }
})
const dynamicCitySelector = computed(() => {
  if (percentage.value < 1 || !percentage.value) {
    return {
      opacity: (1 - percentage.value) * 0.9,
      color: '#000',
      backgroundColor: '#fff',
    }
  } else if (percentage.value === 1) {
    return { color: '#fff', backgroundColor: '#111' }
  }
})

const goCityPage = () => {
  router.push({ name: 'city' })
}
const imgRef = ref(null)
</script>

<style lang="less" scoped>

.citySelector {
  position: fixed;
  left: 10pX;
  color: #fff;
  min-width: 50pX;
  padding: 0 5pX 0 5pX;
  font-size: 13px;
  background-color: rgba(0, 0, 0, 0.2);
  border-radius: 15pX;
  text-align: center;
  border: none;
  outline-style: none;
  z-index: 99;
}
.titlebar {
  position: fixed;
  background-color: var(--themeColor);
  min-width: 280pX;
  width: 100vw;
  z-index: 9;
  font-size: 18pX;
  text-align: center;
  transform: translateZ(0);
  color: #fff;
}
.banner-image{
  width: 100%;
}
.tab-list{
  width: 100vw;
}
.my-swipe{
  height: 56vw;
}

</style>